import { createWebHistory, createRouter } from "vue-router";

const routes =  [
  {
    path: "/",
    alias: "/home",
    name: "home",
    component: () => import("./components/WelcomeVAIS")
  },
  {
    path: "/login",
    name: "login",
    component: () => import("./components/Loginv2")
  },
  {
    path: "/announcements",
    name: "announcements",
    component: () => import("./components/DataAnnouncement")
  },
  {
    path: "/lupa-password",
    name: "lupa-password",
    component: () => import("./components/LupaPassword")
  },
  {
    path: "/ganti-password",
    name: "ganti-password",
    component: () => import("./components/GantiPassword")
  },
  {
    path: "/indeks-prestasi",
    name: "indeks-prestasi",
    component: () => import("./components/NilaiMhs")
  },
  {
    path: "/daftar-ulang",
    name: "daftar-ulang",
    component: () => import("./components/DaftarUlang")
  },
  {
    path: "/dosen",
    name: "daftarDosen",
    component: () => import("./components/DataDosen")
  },
  {
    path: "/mahasiswa",
    name: "daftarMahasiswa",
    component: () => import("./components/DataMahasiswa")
  },
 
  {
    path: "/profile",
    name: "profileUser",
    component: () => import("./components/UserProfile")
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});


router.beforeEach((to, from, next) => {
  const publicPages = ['/login', '/lupa-password', '/dosen', '/mahasiswa'];
  const authRequired = !publicPages.includes(to.path);
  const loggedIn = localStorage.getItem('jwt');

  // trying to access a restricted page + not logged in
  // redirect to login page
  if (authRequired && !loggedIn) {
    next('/login');
  } else {
    next();
  }
});


export default router;
