import axios from 'axios';
import AutentikasiHeader from './AutentikasiHeader';


class AutentikasiService {
  async getProfile() {
    return await axios.get('/api/v2/profile', { headers: AutentikasiHeader() });
  }

  async login(user) {

    const headers = { 
      "Content-Type": "application/json"
    };

    return await axios
      .post('/api/v2/login', {
        username: user.username,
        password: user.password
      }, { headers })
      .then(response => {
        if (response.data.result.accessToken) {
          localStorage.setItem('jwt', JSON.stringify(response.data));
        }
        return response.data;
      });
  }

  async resetPassword(user) {
    return await axios
      .post('/api/v2/reset-password', {
        username: user.username,
        answer: user.answer
      })
      .then(response => {
        return response.data;
      });
  }

  logout() {
    localStorage.removeItem('jwt');
  }

}

export default new AutentikasiService();